import React, {useState, useEffect} from 'react';
import '../static/css/portfolio.css';
import svgLogo from '../static/svg__';
import Button from '@material-ui/core/Button';
import {Tween, ScrollTrigger} from 'react-gsap';

const Portfolio = () => {

    const [portfolioThumb,
        setPortfolioThumb] = useState(null)

    useEffect(() => {
        setPortfolioThumb(portFolioThumnail)

        fetch('/api/testimonial?limit=4&page=2')
            .then(response => response.json())
            .then(data => console.log(data));

    }, [])

    return (
        <ScrollTrigger start="200px center" end="200px center" scrub={1}>
            <Tween
                from={{
                background: "#000"
            }}
                to={{
                background: "#fff"
            }}>

                <section id="portfolio">
                    <div className="container">
                        <div className="portfolio-title">
                            {svgLogo}
                            <h1>PORTFOLIO</h1>
                        </div>
                        <p className="portfolio-title-extra">( Check out some of the ideas we realized so far )</p>
                    </div>

                    <div className="container">
                        <div className="portfolios-gellary">

                            {portfolioThumb && portfolioThumb.map((item, index) => {
                                return <a href={item.urlLink} key={index} target="_blank" rel="noreferrer">
                                    <div
                                        className="portfolios-img-box"
                                        style={{
                                        backgroundImage: `url(${item.thumnail})`
                                    }}>
                                        <Button className="visit_btn">
                                            Visit Now
                                        </Button>
                                    </div>
                                </a>
                            })
}

                        </div>
                    </div>
                </section>
            </Tween>
        </ScrollTrigger>
    )
}

export default Portfolio;

